.fp-enabled {
  .fp-tableCell, .header {
    @include clr; } }

.sections-wrapper {
  position: relative;
  overflow-x: hidden; }

.iScrollIndicator {
  background-color: $blue !important; }

.section {
  &_investment,
  &_career {
    .section__inner {
      background-color: $darkBg; }
    .section__left {
      background-repeat: no-repeat; } }
  &_investment,
  &_career,
  &_development {
    .section__inner {
      overflow: hidden;
      @include sm {
        flex-direction: column; } }
    .section__right,
    .section__left {
      @include sm {
        @include flex-size(100%); } }
    .section__right {
      @include sm {
        padding-right: 30px; } } }
  &_investment {
    position: relative;
    min-height: inherit;
    z-index: 0;
    @include hover {
      z-index: 3; }
    @include sm {
      z-index: 1; }
    .section__outer {
      @include sm {
        padding-top: 0; } }
    .section__title-md {
      max-width: 700px; }
    .section__left {
      @include flex-size(32.1%);
      @include md {
        margin-left: -45px;
        @include flex-size(39%); }
      @include sm {
        margin-left: -39px; } }
    .section__bg {
      background-position: -2px -3%; }
    .section__right {
      @include flex-size(67.9%);
      @include md {
        padding-left: 45px;
        padding-right: 45px;
        min-height: 867px;
        @include flex-size(61%); }
      @include sm {
        padding-left: 0;
        min-height: auto; } } }
  &_career {
    pointer-events: auto;
    min-height: calc(100vh - 135px);
    position: relative;
    z-index: 2;
    margin-top: 92px;
    @include md {
      min-height: calc(100vh - 121px);
      margin-top: 0; }
    @include sm {
      margin-bottom: 77px;
      min-height: inherit; }
    @include hover {
      z-index: 3; }
    .section__outer {
      padding-top: 0;
      @include md {
        padding-top: 60px; }
      @include sm {
        padding-top: 37px; } }
    .section__left {
      @include flex-size(32.1%);
      @include md {
        @include flex-size(33%); } }
    .section__bg {
      background-position: 47px 180px;
      background-size: 422px 780px;
      @include md {
        background-position: -11px 191px;
        background-size: 364px 674px; }
      @include sm {
        background-size: 163px 296px;
        background-position: 23% 7px; } }
    .section__right {
      @include flex-size(67.9%);
      @include md {
        padding-left: 45px;
        @include flex-size(66%);
        min-height: 748px; }
      @include sm {
        padding-left: 0;
        min-height: 0; } } }
  &_contacts {
    color: $white;
    .section__title {
      letter-spacing: 0.04em;
      line-height: 1.28; }
    .section__inner {
      padding-left: 77px;
      background-color: $blue;
      @include xxl {
        padding-left: 30px; }
      @include md {
        padding-left: 68px; }
      @include sm {
        padding-left: 54px; } }
    .section__left {
      @include md {
        padding-right: 37px;
        padding-bottom: 53px;
        @include flex-size(377px); }
      @include sm {
        padding-right: 0;
        @include flex-size(100%); } }
    .section__right {
      background-color: $darkBlue;
      padding-left: 0;
      @include vertical;
      @include sm {
        margin-left: -54px;
        order: initial;
        min-height: 100vh; } }
    .section__outer {
      @include md {
        padding-top: 120px;
        padding-left: 0;
        padding-right: 0; }
      @include sm {
        padding-top: 0;
        display: block; } } }
  &_development {
    height: auto;
    position: absolute;
    margin-top: 85px;
    top: 50vh;
    left: 12.1%;
    z-index: 1;
    width: 100%;
    min-height: inherit;
    min-height: calc(100vh - 135px);
    @include xl {
      min-height: calc(100vh - 121px); }
    @include md {
      position: static;
      margin-top: 0;
      min-height: inherit;
      .section__outer {} }
    @include hover {
      z-index: 3; }
    .section__outer {
      padding-top: 0;
      @include md {
        padding-top: 60px; }
      @include md {
        padding-top: 37px; } }
    .section__inner {
      padding-left: 77px;
      background-color: $textLight;
      @include md {
        padding-left: 43px; } }
    .section__left {
      @include flex-size(764px);
      @include lm {
        @include flex-size(50%); }
      @include md {
        padding-bottom: 130px;
        @include flex-size(460px); }
      @include sm {
        padding-right: 45px;
        padding-bottom: 80px;
        height: auto; } }
    .section__right {
      @include lm {
        @include flex-size(50%); }
      @include md {
        @include flex-size(calc(100% - 460px)); }
      @include sm {
        margin-left: -30px;
        margin-right: 30px;
        margin-bottom: 82px;
        height: 250px;
        &:before {
          transform: scaleX(0);
          transform-origin: 0 0;
          transition: transform $tr 0.3s;
          position: absolute;
          content: "";
          bottom: -33px;
          left: 50%;
          margin-left: -39%;
          height: 1px;
          width: 78%;
          background-color: #272F38; }
        &.is-animate {
          &:before {
            transform: scaleX(1); } } } }
    .section__title-md {
      @include sm {
        margin-top: 0; } }
    .section__development-btn {
      @include sm {
        margin-left: -32px;
        margin-right: -32px;
        text-align: center; } } }
  &_selling {
    .section__title-lg {
      margin-top: 26px;
      @include md {
        margin-bottom: 56px; }
      @include sm {
        margin-top: 53px;
        margin-bottom: 63px; } }
    .section__left {
      @include cover;
      @include md {
        padding-bottom: 80px; }
      @include sm {
        padding-bottom: 0; } }
    .section__text {
      margin-top: 40px;
      margin-bottom: 27px;
      @include xl {
        margin-top: 30px; }
      @include sm {
        margin-top: 42px; } }
    .section__outer {
      @include sm {
        padding-bottom: 100px; } }
    .section__right {
      overflow: hidden; } }
  &__outer {
    padding-top: 135px;
    @include xl {
      padding-top: 120px; }
    @include md {
      padding-top: 120px; }
    @include sm {
      padding-top: 0; } }
  &__inner {
    min-height: calc(100vh - 135px);
    display: flex;
    flex-wrap: wrap;
    @include xl {
      min-height: calc(100vh - 121px); }
    @include md {
      min-height: inherit; }
    @include sm {
      flex-direction: column; } }
  &__left {
    @include flex-size(472px);
    padding: 0 88px 0 0;
    @include xxl {
      padding-right: 30px; }
    @include md {
      @include flex-size(391px);
      padding-right: 66px; }
    @include sm {
      @include flex-size(100%);
      min-height: 225px;
      padding-right: 0; }
    &_border {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 45px;
        bottom: 37px;
        width: 1px;
        background-color: $textLight;
        @include sm {
          display: none; } } }
    &_border-dark {
      &:after {
        background-color: $text;
        @include md {
          display: none; } } } }
  &__right {
    padding-left: 156px;
    flex-grow: 1;
    position: relative;
    background-repeat: no-repeat;
    @include xxl {
      padding-left: 100px;
      background-position: center;
      background-size: cover; }
    @include sm {
      @include flex-size(100%);
      order: -1;
      min-height: 225px;
      padding-left: 0; }
    .section_contacts & {
      overflow: hidden; } }
  &__splitter-wrapper {
    overflow: hidden; }
  &__splitter-horiz {
    height: 1px;
    width: 100%;
    background-color: $text;
    &_white {
      background-color: $textLight; } }
  &__title-lg {
    margin-bottom: 50px;
    @include xl {
      margin-bottom: 30px; } }
  &__title-md {
    margin-top: 200px;
    margin-bottom: 43px;
    @include xl {
      margin-top: 140px; }
    @include lm {
      margin-top: 50px; }
    @include md {
      margin-top: 136px; }
    @include sm {
      margin-top: 100px;
      margin-bottom: 50px; } }
  .section__text {
    margin-bottom: 39px;
    @include md {
      margin-top: 40px;
      padding-right: 5px;
      margin-bottom: 54px; }
    p:not(:last-child) {
      margin-bottom: 28px;
      @include xl {
        margin-bottom: 15px; }
      @include md {
        margin-bottom: 26px; } } }

  .fp-tableCell {
    vertical-align: top; }
  &__download-btn-wrapper {
    margin-left: -25px;
    margin-top: -15px;
    @include xl {
      margin-top: -25px;
      margin-bottom: -25px; }
    @include sm {
      margin-top: -30px;
      margin-bottom: -30px;
      text-align: center; } }
  &__download-btn-area {
    position: relative;
    display: block;
    height: 100px;
    width: 100px;
    .circle-btn {
      backface-visibility: hidden;
      position: absolute;
      top: 25px;
      left: 25px;
      z-index: 3; } }
  &__hr-btn {
    margin-top: 77px;
    @include xl {
      margin-top: 30px; }
    @include md {
      margin-top: 60px; }
    @include sm {
      margin-left: -31px;
      margin-right: -31px;
      text-align: center; } }
  &__adress {
    margin-bottom: 60px;
    @include xxl {
      margin-bottom: 35px; }
    @include xl {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 60px; }
    .adress:not(:last-child) {
      margin-bottom: 19px;
      @include xl {
        margin-bottom: 16px; }
      @include md {
        margin-bottom: 26px; } } }
  &__contacts-title {
    margin-top: 60px;
    margin-bottom: 42px;
    @include xl {
      margin-top: 30px;
      margin-bottom: 20px; }
    @include md {
      margin-top: 98px;
      margin-bottom: 57px; }
    @include sm {
      margin-top: 74px;
      margin-bottom: 46px; } }
  &__connections {
    margin-top: 50px;
    @include xxl {
      margin-top: 35px; }
    @include xl {
      margin-top: 30px; }
    @include md {
      margin-top: 48px; }
    @include sm {
      margin-top: 57px; }
    .connection {
      &:not(:last-child) {
        margin-bottom: 20px;
        @include xl {
          margin-bottom: 10px; }
        @include md {
          margin-bottom: 20px; }
        @include sm {
          margin-bottom: 30px; } } } }
  &__map-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1209px;
    height: 776px;
    margin: -348px 0 0 -652px;
    @include md {
      margin: -383px 0 0 -652px; } }
  &__map {
    max-width: 100%;
    display: inline-block;
    vertical-align: middle; }
  &__bg {
    @include coverdiv;
    @include cover; }
  &__bg-wrapper {
    @include coverdiv;
    position: absolute !important; } }
